Mock.mock(/^\/api\/blog(\?.+)?$/, "get", function (options) {
    const result = qs.parse(options.url);
    return Mock.mock({
        "code": 0, // 
        "msg": "", // 
        "data": {
            "total|0-2000": 786, //
            [`rows|${result.limit}`]: [ // 
                {
                    "id|+1": 2,
                    "title": "@ctitle(1, 50)",
                    description: `@cparagraph(1, 3)`,
                    category: { //
                        "id|0-10": 1,
                        name: "@id"
                    },
                    "scanNumber|1-200": 10,
                    "commentNumber|1-200": 30,
                    "thumb|1": [Mock.Random.image("300x250", "#abc", "#4A7BF7", "Random Image"), null],
                    createDate: `@date('T')`
                }
            ]
        }
    })
})




